<template>
	<div class="modifylogin-wrapper">
		<el-form :label-position="labelPosition" label-width="80px" :model="formLabelAlign">
			<el-form-item label="企业代码">
				<el-input v-model="formLabelAlign.name" placeholder="请输入企业代码"  class = "modify_input" ></el-input>
			</el-form-item>
			<el-form-item label="原密码">
				<el-input v-model="formLabelAlign.region" placeholder="请输入旧密码"  class = "modify_input"></el-input>
			</el-form-item>
			<el-form-item label="新密码">
				<el-input v-model="formLabelAlign.type" placeholder="请输入新密码"  class = "modify_input"></el-input>
			</el-form-item>
			<el-form-item label="确认密码">
				<el-input v-model="formLabelAlign.queren" placeholder="请重新输入密码"   class = "modify_input"></el-input>
			</el-form-item>
		</el-form>
		<div class="modifylogin-button">
			<router-link :to="{path:'/index'}" tag="el-button" type="primary" class="modifylogin-but">提交</router-link>
		</div>
	</div>
</template>

<script>

	export default {
		data(){
			return {
				labelPosition: 'right',
				formLabelAlign: {
				name: '',
				region: '',
				type: '',
				  queren: ''
				}
			}
		}

	}
</script>
<style lang = "less" scoped>
.modifylogin-wrapper{
	width:500px;
	height:400px;
	margin:50px auto;
	.modify_input{
		width:250px;
	}
	.modifylogin-button{
		width: 340px;
		margin: 10px auto;
		.modifylogin-but{
			width:250px;
			height:40px;
			background:blue;
			color: #fff;
		}
	}
}

</style>



